.App {
  display: flex;
  align-self: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
  height: 100vh;
  gap: 2rem;
}

.card-container {
  display: flex;
  justify-content: center;
  flex-grow: 2;
  align-self: center;
}

.card {
  align-self: center;
  height: 100%;
  width: 100vw;
  border: 3px black solid;
  background-color: cornflowerblue;
}


.footer {
  background-color: grey;
  padding: 1rem;
}